		<view class="inputnumbox">
			<!-- <input type="text" confirm-type="done" @input="inputnum" v-model=types focus="true" placeholder="类型" adjust-position="false"
			 style="font-size: 68rpx;height: 70rpx;" /> -->
			   <xfl-select 
			 			             :list="list"
			 			             :clearable="false"
			 			             :showItemNum="5" 
			 			             :listShow="false"
			 			             :isCanInput="true"  
			 			             :style_Container="'height: 50px; font-size: 16px;'"
			 			             :placeholder = "'请选择'"
			 			             :selectHideType="'hideAll'"
									 v-model='types'
									 @change = change
			 			         >
			 			         </xfl-select>
			<input type="number" confirm-type="done" @input="inputnum" v-model=money placeholder="输入金额(必填)" adjust-position="false"
			 style="font-size: 48rpx;margin-top: 30rpx;height: 70rpx;background-color: #F4F5F6;padding: 8rpx;" />
			<input v-model="beizhu" type="text" maxlength=15 placeholder="备注" style="font-size: 48rpx;margin-top: 30rpx;height: 70rpx;background-color: #F4F5F6;padding: 8rpx;" />
			 
		</view>
		<view class="text-area">
			<button type="primary" class="queren" @click="queren">确认</button>

		</view>
		<view class="text-area">
			<button type="warn" class="qingchu" @click="clears()">清除</button>

		</view>
	</view>
</template>

<script>
	import xflSelect from '../../components/xfl-select/xfl-select.vue';  
	export default {
		components: { xflSelect }, 
