				],
				title: 'Hello',
				types: "",
				money: "",
				storeMoney:uni.getStorageSync("inmoney")||0,
			}
		},
		onLoad() {


		},
		methods: {
			change({newVal, oldVal, index, orignItem}){
							// console.log(newVal, oldVal, index, orignItem);
							this.types = newVal;
						},
			// 确认
			queren(){
				if(this.money==""){
					
				}else{
					try {
						if(this.types==""){
							this.types = this.list[2];
						}
					    uni.setStorageSync('inmoney', parseFloat(this.storeMoney)+parseFloat(this.money));
					} catch (e) {
					    console.log(e);
					}
					uni.navigateBack();
				}
				
			},
			// 清除所有
			clears() {
				// this.types = "";
				this.money = "";
				this.beizhu = "";
				
			},
			// 输入时
			inputnum() {
// 输入时
			
			
			},
			
		}
	}
</script>

